.nav {
    width: 100vw;
    height: auto;
    background-color: #1a2b3c;
    color: whitesmoke;
    padding: 1rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}

/* -------------------MENU ----------------- */
.nav-menu {
    visibility: hidden;
    width: 0px;
    height: 0px;
}

.nav-menu>img {
    width: 20px;
}

/* -------------------LOGO ----------------- */
.nav-logo {
    flex: 1;
    font-family: 'Titan One', cursive;
    font-size: 1.5rem;
}

/* -------------------LIST----------------- */
.nav-list {
    flex: 3;
}

.nav-list>ul {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-evenly;
}

/* -------------------OPTIONS----------------- */
.nav-options {
    flex: 2;
    display: flex;
    justify-content: flex-end;
    gap: 3em;
}

.nav-options>button {
    background-color: #1a2b3c;
    color: whitesmoke;
    border: none;
    padding: .1rem;
    display: flex;
    /* justify-self: center; */
    align-items: center;
    gap: .7rem
}

.nav-darkMode {
    width: 24px;
    aspect-ratio: 1;
}

.nav-option {
    width: 20px;
    height: 20px;
}

@media all and (max-width:570px) {
    .nav-menu {
        /* width: 20px; */
        height: 20px;
        visibility: visible;
        flex: 1;
    }

    .nav-list {
        display: none;
    }

    .nav-logo {
        flex: 4;
    }

    .nav-option {
        flex: 2;
    }

    .nav-option {
        display: none;
    }

    .nav-options>button {
        /* background-color: #1a2b3c;
        border: none;
        color: whitesmoke; */
        font-weight: 600;
    }

}